body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Roboto-Light';
  font-style: normal;
  font-weight: 400;
  src: url(/toDoApp/static/media/Roboto-Light.46e48ce0.ttf);
}

@font-face {
  font-family: 'Roboto-Regular';
  font-style: normal;
  font-weight: 400;
  src: url(/toDoApp/static/media/Roboto-Regular.df7b648c.ttf);
}

body{
  font-family: "Roboto-Light";
}
.entrance-page {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100vh;
  background-color: rgb(243, 243, 243);
}

.wrapper-container {
  width: 100%;
  height: 300px;
  background: url(/toDoApp/static/media/goluboy_fon.08e9eaf5.png);
}

.flex-for-div {
  display: -webkit-flex;
  display: flex;
}

.discribin-text,
.form {
  width: 50%;
}

.discribin-text h1,
.discribin-text p {
  font-size: 40px;
  color: white;
}

.discribin-text p {
  font-size: 28px;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.data-user {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.493);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4), 0 10px 10px rgba(0, 0, 0, 0.25);
  font-family: "Roboto-Light";
  color: white;
  height: 70%;
  width: 60%;
}

.data-user input {
  background: none;
  border: none;
  border-bottom: 1px solid #ccc;
  color: white;
  outline: none;
  font-size: 18px;
}

.data-user p {
  font-size: 13px;
  padding-top: 7px;
  margin: 0;
}

.data-user .btn-todo {
  text-decoration: none;
  color: white;
}

@media screen and (max-width: 1025px) {
  .entrance-page {
    display: block;
    background: none;
  }

  .wrapper-container {
    height: auto;
    background: url(/toDoApp/static/media/goluboy_fon_mobile.6267f50d.jpg);
  }

  .container {
    display: block;
  }

  .discribin-text,
  .form {
    width: 100%;
  }

  .discribin-text {
    margin-bottom: 80px;
    padding-left: 20px;
  }

  .data-user {
    height: 200px;
    width: 95%;
  }
}

@media screen and (max-width: 425px) {
  .wrapper-container {
    min-height: 100%;
  }

  .discribin-text {
    margin-bottom: 40px;
    padding-left: 7px;
  }

  .discribin-text h1 {
    font-size: 40px;
  }

  .discribin-text p {
    font-size: 26px;
  }
}
.block-header {
  height: 180px;
  width: 100%;
  background: url(/toDoApp/static/media/app_top.d2aabf52.png) no-repeat;
  background-size: cover;
  color: white;
  box-shadow: 0 7px 7px -6px #111;
}

.block-header h2 {
  padding-top: 80px;
  font-weight: bold;
}

.block-content {
  background-color: #fff;
}

.block-content .container {
  position: relative;
}

.block-content .container::before,
.block-content .container::after {
  content: "";
  z-index: -1;
  position: absolute;
  bottom: 15px;
  left: 20px;
  width: 40%;
  top: 80%;
  background: #777;
  box-shadow: 0 15px 10px #777;
  -webkit-transform: rotate(-2deg);
          transform: rotate(-2deg);
}

.block-content .container::after {
  right: 20px;
  left: auto;
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
}
.task {
  position: relative;
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 1px solid #cacac9;
}

.task:first-child {
  border-top: 1px solid #cacac9;
}

.task .mark-task {
  margin-left: 5px;
}

.task p {
  padding-left: 20px;
  margin: 0;
}

.task p span {
  font-size: 12px;
  color: #818181;
}

.task .btn-belete {
  position: absolute;
  right: 0;
  background: none;
  border: none;
  color: #4a4f53;
  padding: 4px;
  font-family: "Roboto-Regular";
  -webkit-transition: .3s;
  transition: .3s;
  outline: none;
}

.task .btn-belete:hover {
  color: white;
  background-color: #070f85;
}

.status-active,
.status-unActive {
  height: 24px;
  width: 24px;
}
.input-addTask {
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.input-addTask input {
  width: 80%;
  padding-left: 15px;
  font-size: 18px;
  border: none;
  border-bottom: 1px solid#cacac9;
  outline: none;
}
